import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import Projects from '../components/Projects.js';
import ProjectGuide from '../components/ProjectGuide.js';
import Navbar from '../components/Navbar.js';

import medAlarm from '../img/med-alarm.png';
import familyHub from '../img/family-hub-full.png';
import capitalsphere from '../img/Capitalsphere_desktop.png';
import d4sd from '../img/d4sd.png';
import platform from '../img/d4sd_platform.png';
import litoNail from '../img/lito-nail.png';
import logo from '../img/logo-black.png';
import './style.css';

class Home extends Component {
  constructor() {
    super();
    this.state={
      hover: ""
    }
  }

  handleHoverEnter = e => {
    console.log("this.state");
    console.log(e.target);
    this.setState({hover: e.currentTarget.id});
    console.log(this.state);
  }

  handleHoverLeave = e => {
    this.setState({hover: ""});
    console.log("left");
  }

  handleClick = e => {
    const target = document.getElementById("takeaways");
    console.log("target");
    console.log(e.target.title);
    target.scrollIntoView();
  }

  render() {
    return (
      <div className="container">
        <Navbar/>
        <br/><br/><br/><br/><br/><br/>
        <div className="home_title">
          <h1>I'm Yuhan Wang. Welcome to my space!</h1><br/>
          <h4>I see myself as a
            <span style={{"color":"#C58ADE", "fontWeight":"700"}}> UX Specialist </span> and a
            <span style={{"color":"#C58ADE", "fontWeight":"700"}}> Problem solver</span>.
            <br/>
            I am always motivated to
            <span style={{"color":"#C58ADE", "fontWeight":"400"}}> discover the right problem </span>

            and <span style={{"color":"#C58ADE", "fontWeight":"400"}}> create impactful solutions through empathizing</span>.
          </h4>
        </div>
        <br/>
        {/**************************Projects*************************/}
        <div className="project-boxes">
          <div className="row">
            <div className="col-lg-6 col-sm-12 align-self-center">
              <Link to='/lito_nail'>
              <div className="litonail-box text-center">
                <img src={litoNail} alt="litoNail"/>
              </div>
              </Link>
            </div>
            <div className="col-lg-6 col-sm-12 align-self-center">
              <Link to='/d4sd'>
                <div className="d4sd-box text-center">
                  <img src={d4sd} alt="d4sd"/>
                </div>
              </Link>
            </div>
          </div>
          <div className="row">
            <div className="col-lg-6 col-sm-12">
              <Link to='/capitalsphere'>
              <div className="capitalsphere-box text-center">
                <img src={capitalsphere} alt="capitalsphere"/>
              </div>
              </Link>
            </div>
            <div className="col-lg-6 col-sm-12">
              <Link to='/im'>
              <div className="capitalsphere-box text-center">
                <img src={capitalsphere} alt="capitalsphere"/>
              </div>
              </Link>
            </div>
          </div>
        </div>

      </div>
    );
  }
}

export default Home;
