import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class Research extends Component {
  render() {
    return (
      <div id="research">
        <h6>We started our project by interviewing Yuan Jing, our client and the owner of Lito Nail.
          From the interview, we discovered 2 main problems --
          <span style={{"color":"#C58ADE"}}> exclusive customer group</span> and
          <span style={{"color":"#C58ADE"}}> limited ways of marketing</span>.
          The current customer group is mostly consisted of international students and thus
          when it is not school time, there will be few customers since all students are out of town.
          Their main way to attract new customers is through Wechat, a popular Chinese social media, which
          doesn't help to approach to local residents. Therefore, Yuan hoped to use the website to
          attract more residents who lived around San Diego area in order for a more stable customer group.
          We also got to know that Yuan regarded Lito Nail as
          <span style={{"color":"#C58ADE"}}> Luxurious</span> and
          <span style={{"color":"#C58ADE"}}> Elegant</span>.
          <br/><br/>
          After understanding our clients' need, requirements and preferences, we hoped to know what customers
          would say. We then proceeded to conduct interviews on Lito Nail's
          <span style={{"color":"#C58ADE"}}> regular customers</span> (people who've come to Lito Nail at least once) as well as
          <span style={{"color":"#C58ADE"}}> potential customers</span> (people who do manicure but never comes to Lito Nail). Because
          of time constraints, we did 10 interviews in total, half regular customers and half potential ones. Our main
          focus was on people's main considerations on manicures, how people hear about this place, what makes/prevents them
          going here. We made 2 personas from the user interviews.
        </h6>
        <h3>Client and user interviews provide tons of info. It is important to find a optimum solution
            among these ambiguity.</h3>
      </div>
    );
  }
}

export default Research;
