import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './style.css';

class Context extends Component {
  render() {
    return (
      <div id="context">
        <h1>{this.props.title}
          <span className="h4">
            {this.props.subtitle}
          </span>
        </h1>
        <p>
          My Role: {this.props.role}
          <i className="fas fa-tools"></i>{this.props.tool}</p>
        <p>
          <i className="fas fa-user-friends team"></i>{this.props.team}
          <i className="fas fa-clock"></i>{this.props.duration}
        </p>
        <br/>
        <div className="row">
          <div className="col-12">
            <h6 dangerouslySetInnerHTML={{ __html: this.props.intro }} />
          </div>
        </div>
        <br/>
        <div className="brief">
          <div className="row">
            <div className="col-lg-3 col-md-4 col-sm-12">
            </div>
            <div className="col-lg-9 col-md-8">
              <img src={this.props.demo}/>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default Context;
