import React, { Component } from 'react';
import { Link } from 'react-router-dom';

import Navbar from '../components/Navbar';
import Guide from '../components/Guide';
import Context from '../components/Context';
import Research from '../components/Research';
import Ideation from '../components/Ideation';

import iter11 from '../img/d4sd_iter11.png';
import iter12 from '../img/d4sd_iter12.png';
import iter13 from '../img/d4sd_iter13.png';
import iter14 from '../img/d4sd_iter14.png';
import style1 from '../img/d4sd_style1.jpg';
import style2 from '../img/d4sd_style2.jpg';
import style3 from '../img/d4sd_style3.jpg';
import iter2 from '../img/d4sd_iter2.png';
import diagram1 from '../img/d4sd_about1.png';
import diagram2 from '../img/d4sd_about2.png';
import diagram3 from '../img/d4sd_about3.jpg';
import home1 from '../img/d4sd_home1.png';
import home2 from '../img/d4sd_home2.png';
import home3 from '../img/d4sd_home3.png';
import home4 from '../img/d4sd_home4.png';
import home5 from '../img/d4sd_home5.png';
import home6 from '../img/d4sd_home6.png';

import './style.css';

class D4SD extends Component {
  render() {
    return (
        <div className="container project">
          <Navbar/>
          <br/><br/><br/><br/><br/>
          <div className="row">
            {/**Design process**/}
            <div className="col"/>
            <div className="col-lg-10">
              <Context
                title="D4SD"
                subtitle=" -- Design, develop & launch a website for D4SD, a civic design initiative."
                role=" UX Designer & Front-end Developer"
                tool=" Figma, ReactJs"
                duration=" Jun 2019 - Present"
                team=" Kylee Peng, Anthony Bryce Grubbs, Brendan Jew, Steven Dow"
                intro="The main purpose of the website is to promote the initiative D4SD (Design for San Diego).
                I'm responsible for designing, developing and maintaining the website. Collaborating with other
                designers and content strategies, we keep iterating the design of the website for better promotion.
                <br/><br/>
                Design for San Diego (D4SD) is a civic design challenge that
                aims at providing a space for all citizens to address San Diego's
                most challening issues, prototype solutions through human-centered design,
                and link them to resources in their communities that can bring those
                solutions to life."
              />
              <div id="process">
                <div id="problem">
                  <br/><br/>
                  <h3>Challenge</h3>
                  <h6>
                    By talking with professor, I had a general idea about the two most
                    important purposes of the website. Firstly, it should present the main theme
                    for the competition – how can we make San Diego a sustainable city.
                    Equally important, it should have clear call to actions to attract people to
                    get involved in the event – not only as a participant, but also as
                    mentors, educators, volunteers and so on. So <span> how can
                    we explain the event clearly and effectively to different people </span> and <span> how
                    to motivate them to be part of the event</span>? We paid special attention to homepage since
                    it would be the first sight of users.
                  </h6>
                </div>
                <div id="research">
                  <br/><br/>
                  <h3>First Try</h3>
                  <h6>
                    We created several alternative prototypes to see what kind of illustrations
                    is best suitable for the website. <span> I was responsible for finding good realistic
                    images that go along with our main theme, sustainability </span>. In the beginning,
                    I only focus on “sustainable” related words, but as I’m browsing more and more
                    images, I started to think that an inspiring image (such as surfing) might
                    also work for a competition website. I started to focus more on the feeling
                    rather than the concrete content the image needs to convey. <br/>
                    Here are some alternatives that we had.
                  </h6>
                  <div className="row">
                    <div className="col"><img src={iter11} className="w-100"/></div>
                    <div className="col"><img src={iter13} className="w-100"/></div>
                    <div className="col"><img src={iter14} className="w-100"/></div>
                    <div className="col"><img src={iter12} className="w-100"/></div>
                  </div>
                </div>
                <div id="feedback">
                  <br/><br/>
                  <h3>Feedback</h3>
                  <h6>
                    We presented our design to some other event hosts and got feedback that
                    <span> realistic images are too focused on specific content </span> and people might misunderstand
                    the real purpose of the competition. I also saw the potential problem
                    when choosing images but only decided to focus on the feeling over
                    content of the image rather than directly solving the problem.
                    After the problem was explicitly raised, I suddenly thought of
                    that maybe I could’ve found multiple images that represent different
                    aspects of our theme and make them a collage. <span> I learnt that it’s really important to trust your
                    gut and try best to specify any potential problems rather than ignoring them.
                    No hindsight!</span><br/><br/>
                    We also requested feedback from a visual designer and got really valuable
                    input. He thought that, despite a major trend, website pages don’t need to
                    be divided into cards based on different contents, or at least a clear
                    division is not necessary. He suggested that <span>storytelling</span> might be a better way to
                    engage people and convey information in our case.
                    <br/>Thanks to Kylee, we now have some awesome illustrations which we'll later connect them
                    as a story.
                    <div className="row">
                      <div className="col">
                        <img src={home1} alt="nail" className="w-100"/>
                      </div>
                      <div className="col">
                        <img src={home2} alt="nail" className="w-100"/>
                      </div>
                      <div className="col">
                        <img src={home3} alt="final1" className="w-100"/>
                      </div>
                    </div>
                    <div className="row">
                      <div className="col">
                        <img src={home4} alt="final1" className="w-100"/>
                      </div>
                      <div className="col">
                        <img src={home5} alt="final1" className="w-100"/>
                      </div>
                      <div className="col">
                        <img src={home6} alt="final1" className="w-100"/>
                      </div>
                    </div>
                  </h6>
                </div>
                <div>
                  <br/><br/>
                  <h3>About Page</h3>
                  <h6>
                    After having a direction for homepage, we then moved on working on another important page -- the about page.
                    This time we assigned work to different person to be more efficient --
                    2 people for illustrations of homepage, 2 people for content strategy of the whole website and <span>I am responsible
                    for integrating each page and implementing the website with ReactJs</span>. I
                    really love my team and working with them! Although we have different tasks, we work on the same table and ask for
                    feedback from each other frequently.
                    The main purpose of the about page is to <span>introduce the D4SD challenge and encourage all people to take part</span>. Initially,
                    our idea is to put all possible roles and link them to contact us page.
                    <br/><br/>
                    <div className="row align-items-center">
                      <div className="col-lg-6">
                        <h6>Iteration1 -- We displayed all the roles from the event's point of view. We wanted people to
                          choose one they can fit in and learn more about it.
                        </h6>
                      </div>
                      <div className="col-lg-6">
                        <img src={diagram1} alt="diagram1" className="w-100"/>
                      </div>
                    </div>
                    <br/>
                    <div className="row align-items-center">
                      <div className="col-lg-6">
                        <h6>Feedback! -- We presented iter1 to a person who is not familiar with this challenge. She said that she
                          didn't know exactly what these roles are and she had no idea which one she could fit in. She hoped a clearer
                          explanations could be provided. We thus started to think that in order to show our inclusiveness, it would work
                          better if we start from participants' point of view rather than ours'.
                        </h6>
                      </div>
                      <div className="col-lg-6">
                        <img src={diagram3} alt="diagram2" className="w-100"/>
                      </div>
                    </div>
                    <br/>
                    <div className="row align-items-center">
                      <div className="col-lg-6">
                        <h6>Iteration2 -- This time instead of setting specific roles, we tried to include all kinds of people
                          in our diagram to imply that anyone can make an impact in this challenge. We also randomly added
                          empty space which encourage people to see themselves fitting in this challenge.
                        </h6>
                      </div>
                      <div className="col-lg-6">
                        <img src={diagram2} alt="diagram2" className="w-100"/>
                      </div>
                    </div>
                  </h6>
                </div>
                <div>
                  <br/><br/>
                  <h3>Adding Animation -- An awesome learning opportunity!</h3>
                  <h6>
                    After getting the static illustrations, my job was to animate them using ReactJs. I've never done it before and it took
                    me some time searching around different websites aimlessly, trying to find a best way to do it.
                    Soon I realized that I'm always going back to those same websites. I thus stopped searching and instead
                    chose one method to dig in. Without doubt I encountered problems, but I could always quickly solve them from other
                    websites' materials since I've already understood the theory of scrolling animation. <span>I really learnt that don't wait for
                    a best solution, just try things out! Only after starting trying things out can I have the chance to optimize my method</span>.
                  </h6>
                </div>
                <div id="future">
                  <br/><br/>
                  <h3>Further Direction</h3>
                  <h6>Our project is still in progress. We will keep fleshing out the illustrations, making the website
                    responsive, and working on contents to make it easy to understand.
                    We will also send our test versions to potential partners and
                    participants for more feedback and hopefully it will be launched in 2 weeks!
                  </h6>
                </div>
              </div>
            </div>
            <div className="col"/>
          </div>
          <div className="row next_btn">
            <Link to='/lito_nail'>
              Next Project
              <i className="fas fa-chevron-right"></i>
            </Link>
          </div>
        </div>
    );
  }
}

export default D4SD;
